import axios from "axios";
import { useEffect, useState } from "react"
import BookCard from "../bookCard/bookCard";
import './Home.css'
import HeaderDiv from "../HeaderDiv";


export default function HomePage() {
    const [booksData, setBooksData] = useState(null);
    const [Role, setRole] = useState();
    const [reRender, setReRender] = useState(false);




    useEffect(() => {
        const role = JSON.parse(localStorage.getItem('Role'));
        if (role) {
            setRole(role)
        }
        axios.get(`http://localhost:8000/books`)
            .then(books => {
                setBooksData(books.data.bookData)
            })
            .catch(e => {
                console.log("error", e);
            });
    }, [reRender])

    return (
        <>
            <HeaderDiv role={Role} HomePage={"homePage"} />
            <div className="componentRenderDiv">
            {/* <div> */}
                    {booksData && booksData.map((book) => <BookCard properties={book} role={Role} callbackFunction={setReRender} RValue={reRender} />)}
            </div>
        </>
    )
}