import { useState } from "react";
import { Col, Container, Image, Row, Button } from "react-bootstrap";
import EditBook from "../editBook/EditBook";
import axios from "axios";
import './bookCard.css';



export default function BookCard(props) {

    const [editPopupShow, setEditPopupShow] = useState(false);

    const handleEditPopupShow = () => setEditPopupShow(true);
    const handleEditPopupHide = () => setEditPopupShow(false);

    const book = props.properties;
    const callback = props.callbackFunction;
    const RValue = props.RValue;
    const pdfID = props.properties._id;

    const openBook = ()=>{
        let url = `http://localhost:8000/books/read/${props.properties.pdfName}`
        window.open(url, '_blank');
    }

    const deleteBook = async()=>{
        try{
            const deletedBook = await axios.delete(`http://localhost:8000/admin/delete/book/${pdfID}`)
            console.log(deletedBook);
            callback(!RValue);
        }
        catch(error){
            console.log('error',error);
        }
    }

    return (
        <>
            <Container className=" shadow-lg border border-primary mb-4 bookCardStyle" >

                <Row md={4} >
                    <Col xs={6} md={4}>
                        <Image src={`http://localhost:8000/uploads/${props.properties.imgName}`} thumbnail />
                    </Col>
                    <Col xs={6} md={4}>
                        <h3 className="mt-4">{props.properties.name}</h3>
                        <h6>By {props.properties.author}</h6>

                        <Row md={4} className="mt-4" >
                            <Col xs={5} md={5}>Category</Col>
                            <Col xs={5} md={5}>{props.properties.category}</Col>
                        </Row>

                        <Row md={4}>
                            <Col xs={5} md={5}>price</Col>
                            <Col xs={5} md={5}>{props.properties.price}</Col>
                        </Row>
                        <Row md={4} >
                            <Col xs={5} md={5}>Language</Col>
                            <Col xs={5} md={5}>{props.properties.language}</Col>
                        </Row>
                    </Col>
                    <Col xs={6} md={4}>
                        {props.role=="Admin"? (<div className = "h-100 d-flex align-items-center justify-content-center flex-column">
                            <Button className="w-75 m-3" onClick={openBook}>Read Book</Button>
                            <Button className="w-75 m-3" onClick={()=>{handleEditPopupShow()}}>Edit Book</Button>
                            <Button className="w-75 m-3" onClick={deleteBook}>Delete Book</Button>
                        </div>):
                        (<div className = "h-100 d-flex align-items-center justify-content-center flex-column">
                            <Button className="m-3" onClick={openBook}>Read Book</Button>
                        </div>)
                        }
                    </Col>
                </Row>
                <EditBook hidePopup ={handleEditPopupHide} popupShow = {editPopupShow} properties = {book} callback = {callback} RValue = {RValue} />
            </Container>
        </>

    )
}