

import axios from 'axios';
import { Button, Col, Container, Row, Form } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { NavLink, Navigate, redirect, useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import HeaderDiv from '../HeaderDiv';
import { useAuth } from '../protectedRoutes/authContext';

export default function Login() {

    const { login } = useAuth();

    const { register, handleSubmit, formState: { errors } } = useForm();
    const navigate = useNavigate();
    const [loginError, setLoginError] = useState("");

    const role = JSON.parse(localStorage.getItem('Role'));
 
    const formLogin = async (data) => {
        try {
            const response = await axios.post(`http:///localhost:8000/${data.role}/login`, data);
            // console.log(response)
            localStorage.setItem('Role', JSON.stringify(data.role))
            console.log(`${data.role} logged in`);
            login();
            navigate('/home');
        } catch (error) {
            if (error.response) {
                setLoginError(error.response.data.msg);
            } else if (error.request) {
                setLoginError("No response received from the server");
            } else {
                setLoginError("Error while sending the request");
            }
            console.log("error", error);
        };
    }

    return (
        <>
            {role ?
                <Navigate to="/home" />
                : <>
                    <HeaderDiv />
                    <Container className='shadow-lg border p-4 w-50'>
                        <Form onSubmit={handleSubmit(formLogin)}>
                            <h2 className='text-center pb-3'>Login</h2>
                            <div key={`default-radio`} className=" d-flex m-2 flex-raw align-item-center justify-content-center">
                                <Form.Check type={"radio"} id={`default-radio1`} label={`Admin`} className="mx-2" {...register("role", { required: "Role Not Selected" })} value="Admin" />
                                <Form.Check type={"radio"} id={`default-radio2`} label={`User`} className="mx-2" {...register("role", { required: "Role Not Selected" })} value="User" />
                            </div>
                            <div className="text-center mb-3">
                                {errors.role && <Form.Text className='text-danger'>{errors.role.message}</Form.Text>}
                            </div>
                            <Form.Group as={Row} className="mb-3" controlId="exampleForm.ControlInput1">
                                <Form.Label column sm="3">
                                    Email address
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="email" placeholder="name@example.com" {...register("email", { required: "Email is required" })} />
                                    {errors.email && <Form.Text className='text-danger'>{errors.email.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formBasicPassword">
                                <Form.Label column sm="3">
                                    Password
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="password" placeholder="Password" {...register("password", { required: "Password is required" })} />
                                    {errors.password && <Form.Text className='text-danger'>{errors.password.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3 text-center" controlId="formBasicErrorMessage">
                                {loginError && <Form.Text className='text-danger'>{loginError}</Form.Text>}
                            </Form.Group>


                            <Form.Group className='text-center'>
                                <Button variant="primary" type='submit' >Submit</Button>
                            </Form.Group>
                            <div className='m-2 text-center'>
                                Don't have Account? <NavLink to="/register">SignUp</NavLink>
                            </div>
                        </Form>
                    </Container>
                </>
            }
        </>

    )
}