import axios from "axios";
import { Button, Col, Container, Form, Modal, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";

export default function EditBook(props) {
    const { register, handleSubmit, formState: { error } } = useForm();

    const id = props.properties._id;
    const formEditBook = async (data) => {
        const formData = new FormData();

        formData.append("name", data.name);
        formData.append("category", data.category);
        formData.append("price", data.price);
        formData.append("author", data.author);
        formData.append("language", data.language);

        if (data.imageName && data.imageName[0]) {
            formData.append("imageName", data.imageName[0]);
        }
        if (data.pdfN && data.pdfN[0]) {
            formData.append("pdfN", data.pdfN[0]);
        }

        try {
            const response = await axios.put(`http://localhost:8000/admin/book/edit/${id}`, formData, {
                headers: {'content-type': 'multipart/formdata'}
            });
            props.callback(!props.RValue);
            props.hidePopup();
            console.log('Upload successful:', response.data);
        }
        catch (e) {
            console.error('Error uploading files:', error);
        }
    }

    return (
        <>
            <Modal show={props.popupShow} onHide={props.hidePopup}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit Book</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Container className='border p-4 w-100'>
                        <Form onSubmit={handleSubmit(formEditBook)}>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextName">
                                <Form.Label column sm="3">
                                    Name
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Name" defaultValue={props.properties.name} {...register("name")} />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextCategory">
                                <Form.Label column sm="3">
                                    Category
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Category" defaultValue={props.properties.category} {...register("category")} />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextPrice">
                                <Form.Label column sm="3">
                                    Price
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="Number" placeholder="Price" defaultValue={Number(props.properties.price)} {...register("price")} />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextAuthor">
                                <Form.Label column sm="3">
                                    Author
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Author" defaultValue={props.properties.author} {...register("author")} />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextLanguage">
                                <Form.Label column sm="3">
                                    Language
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Language" defaultValue={props.properties.language} {...register("language")} />
                                </Col>
                            </Form.Group>
                            {/* image */}
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextImage">
                                <Form.Label column sm="3">
                                    Image
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="file" placeholder="Image"  {...register("imageName")} />
                                    {props.properties.imgName && (<p>Current Image: {props.properties.imgName}</p>)}
                                </Col>
                            </Form.Group>
                            {/* pdf */}
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextPdf">
                                <Form.Label column sm="3">
                                    Book Pdf
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="file" placeholder="Pdf"  {...register("pdfN")} />
                                    {props.properties.pdfName && (<p>Current pdf: {props.properties.pdfName}</p>)}
                                </Col>
                            </Form.Group>
                            <div className='text-center'>
                                <Button variant="primary" type='submit' >Submit</Button>
                            </div>
                        </Form>
                    </Container>
                </Modal.Body>
            </Modal>
        </>
    )
}


