import { Button, Container } from 'react-bootstrap';
import './HeaderDiv.css'
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import AddBook from './addBook/addBook';


export default function HeaderDiv(props) {

    const [addBookPopupShow, setAddBookPopupShow] = useState(false);

    const handleAddBookPopupShow = () => setAddBookPopupShow(true);
    const handleAddBookPopupHide = () => setAddBookPopupShow(false);


    const homePage = props.HomePage;
    const addBookPage = props.addBookPage;
    const navigate = useNavigate();

    const navigation = () => {
        if (homePage) {
            navigate("/addbook")
        }
        if (addBookPage) {
            navigate("/home")
        }
    }
    const logout = () => {
        localStorage.removeItem('Role');
        navigate('/login');
    }

    const Role = props.role;
    return (
        <>
            <Container className="m-3 headerMainDiv">

                <img src="logo.svg" className="mainLogo mb-5" alt="Logo" />
                {Role && (Role == "Admin" ?
                    <>
                        <i class="ri-shield-user-fill headerUserLogo">{Role}</i>
                        <div className='headerAddBookButton'>
                            {homePage && <Button variant="primary" onClick={handleAddBookPopupShow}>Add Book</Button>}
                            {/* {addBookPage && <Button variant="primary" onClick={navigation}>Home</Button>} */}
                        </div>
                        <i class="ri-logout-circle-r-line headerLogoutLogo" onClick={logout}></i>
                        {/* <AddBook hidePopup ={handleAddBookPopupHide} popupShow = {addBookPopupShow} /> */}
                    </>

                    :
                    <>
                        <i className="ri-user-line headerUserLogo">{Role}</i>
                        <i class="ri-logout-circle-r-line headerLogoutLogo" onClick={logout}></i>
                    </>)
                }
                <AddBook hidePopup={handleAddBookPopupHide} popupShow={addBookPopupShow} />
            </Container>
        </>
    )
}


