import { Button, Col, Container, Row, Form, Modal } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import HeaderDiv from '../HeaderDiv';

export default function AddBook(props) {
    const { register, handleSubmit, formState: { errors } } = useForm();

    const Role = JSON.parse(localStorage.getItem("Role"))

    const formAddBook = async (data) => {
        const formData = new FormData();

        formData.append("name", data.name);
        formData.append("category", data.category);
        formData.append("price", data.price);
        formData.append("author", data.author);
        formData.append("language", data.language);
        formData.append("imageName", data.imageName[0]);
        formData.append("pdfN", data.pdfN[0]);

        try {
            const response = await axios.post('http://localhost:8000/admin/book/add', formData, {
                headers: { 'Content-Type': 'multipart/form-data' }
            });
            props.hidePopup();
            console.log('Upload successful:', response.data);
        }
        catch (e) {
            console.error('Error uploading files:', e);
        }
    }

    return (
        <>
            <Modal show={props.popupShow} onHide={props.hidePopup}>
                <Modal.Header closeButton>
                    <Modal.Title>Add Book</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Container className='border p-4 w-100'>
                        <Form onSubmit={handleSubmit(formAddBook)}>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextName">
                                <Form.Label column sm="3">
                                    Name
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Name" {...register("name",{ required: "Name Required" })} />
                                    {errors.name && <Form.Text className='text-danger'>{errors.name.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextCategory">
                                <Form.Label column sm="3">
                                    Category
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Category" {...register("category",{ required: "Category Required" })} />
                                    {errors.category && <Form.Text className='text-danger'>{errors.category.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextPrice">
                                <Form.Label column sm="3">
                                    Price
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="Number" placeholder="Price" {...register("price",{ required: "Price Required" })} />
                                    {errors.price && <Form.Text className='text-danger'>{errors.price.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextAuthor">
                                <Form.Label column sm="3">
                                    Author
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Author" {...register("author",{ required: "Author Required" })} />
                                    {errors.author && <Form.Text className='text-danger'>{errors.author.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextLanguage">
                                <Form.Label column sm="3">
                                    Language
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="text" placeholder="Language" {...register("language",{ required: "Language Required" })} />
                                    {errors.language && <Form.Text className='text-danger'>{errors.language.message}</Form.Text>}
                                </Col>
                            </Form.Group>

                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextImage">
                                <Form.Label column sm="3">
                                    Image
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="file" placeholder="Image"  {...register("imageName",{ required: "Image Required" })} />
                                    {errors.imageName && <Form.Text className='text-danger'>{errors.imageName.message}</Form.Text>}
                                </Col>
                            </Form.Group>

                            <Form.Group as={Row} className="mb-3" controlId="formPlaintextPdf">
                                <Form.Label column sm="3">
                                    Book Pdf
                                </Form.Label>
                                <Col sm="9">
                                    <Form.Control type="file" placeholder="Pdf"  {...register("pdfN",{ required: "Pdf Required" })} />
                                    {errors.pdfN && <Form.Text className='text-danger'>{errors.pdfN.message}</Form.Text>}
                                </Col>
                            </Form.Group>
                            <div className='text-center'>
                                <Button variant="primary" type='submit' >Submit</Button>
                            </div>
                        </Form>
                    </Container>
                </Modal.Body>
            </Modal>


        </>
    )
}